<template>
    <v-layout align-center justify-center>
        <v-flex xs12 sm8 md6 lg5 x14>
            <v-card>
                <v-toolbar dark color="primary">
                    <v-toolbar-title>
                        Acceso al sistema
                    </v-toolbar-title>
                </v-toolbar>
                <v-card-text>
                    <v-text-field
                        v-model="email"
                        autofocus
                        color="secondary"
                        label="Email"
                        required
                    ></v-text-field>
                    <v-text-field
                        v-model="password"
                        type="password"
                        color="secondary"
                        label="Password"
                        required
                    ></v-text-field>
                    <v-flex class="red--text" v-if="errorM,dialog == false">
                        {{errorM}}
                    </v-flex>
                </v-card-text>
                <v-card-actions class="px-3 pb-3">          
                   <template>
                        <div class="text-center">
                            <v-btn
                            :disabled="dialog"
                            :loading="dialog"
                            class="white--text"
                            color="primary"
                            @click="dialog = true,ingresar()"
                            >
                            Ingresar
                            </v-btn>
                            <v-dialog
                            v-model="dialog"
                            hide-overlay
                            persistent
                            width="300"
                            >
                            <v-card
                                color="primary"
                                dark
                            >
                                <v-card-text>
                                Por favor espera
                                <v-progress-linear
                                    indeterminate
                                    color="black"
                                    class="mb-0"
                                ></v-progress-linear>
                                </v-card-text>
                            </v-card>
                            </v-dialog>
                        </div>
                        </template>
                </v-card-actions>
            </v-card>
        </v-flex>
    </v-layout>
</template>
<script>
import axios from 'axios';
    export default{
        data(){
            return{
                email:'',
                password:'',
                errorM:null,
                dialog:false
            }
        },
         watch: {
            dialog (val) {
                if (!val) return

                setTimeout(() => (this.dialog = false), 4000)
            },
            },
        methods: {
            ingresar(){
                
                axios.post('usuario/login',{email:this.email,password:this.password})
                .then(response=>{
                   return response.data
                })
                .then(data =>{
                    this.$store.dispatch("guardarToken",data.tokenReturn);
                    this.$router.push({name:'Home'});
                })
                .catch(error=>{
                    //console.log(error)
                    this.errorM=null;
                    if(error.response.status == 404){
                        this.errorM="No existe el usuario o no has ingresado las credenciales correctamente"
                    }else{
                        this.errorM="Error en el servidor"
                    }
                })
            }
        }
    }
</script>
